header {
  height: 60px; }

#Header {
  background: white; }
  #Header .container {
    height: 100%;
    display: flex;
    align-items: center; }
    #Header .container .item:not(.item1) + .item:not(.item2) {
      padding: 20px 13px; }
    #Header .container .item2, #Header .container .item4 {
      position: relative; }
    #Header .container .item2 {
      flex-grow: 1;
      padding-right: 10px; }
  #Header .countCart {
    width: 16px;
    height: 16px;
    background: #aaa;
    color: white;
    border-radius: 50%;
    font-size: 10px;
    text-align: center;
    position: absolute;
    right: 3px;
    top: 14px;
    font-weight: 500;
    padding-top: 1px; }
  #Header .logo {
    width: 140px;
    height: 100%;
    margin-top: 25px; }
  #Header .search {
    width: 100%;
    height: 36px;
    padding-left: 45px;
    border: 1px solid #dddddd8c;
    border-radius: 5px; }
  #Header ::placeholder {
    font-size: 15px;
    font-weight: 400; }
  #Header .isearch {
    position: absolute;
    top: 12px;
    left: 13px;
    color: #ababab;
    font-size: 14px; }

#special-header-promo {
  background: #4dbc15; }

@media print {
  .hbs *, .hbs *:before, .hbs *:after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important; } }

.hbs * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.hbs *:before, .hbs *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

::selection {
  background: #4dbc15;
  color: #fff; }

::-moz-selection {
  background: #4dbc15;
  color: #fff; }

#special-header-promo {
  position: relative;
  top: 2px;
  height: 24px;
  font-size: 0;
  width: 68px; }

#special-header-promo .sale-upper-tri {
  position: absolute;
  top: -1px;
  left: -1px;
  width: 0;
  height: 0;
  border-top: 5px solid #fff;
  border-right: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid #fff; }

#special-header-promo .sale-bottom-tri {
  position: absolute;
  bottom: -1px;
  left: -1px;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #fff;
  border-left: 5px solid #fff; }

#special-header-promo .sale-hole {
  position: absolute;
  top: 50%;
  left: 4px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #fff;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%); }

#special-header-promo .sale-rectangle {
  height: 24px;
  position: relative; }

#special-header-promo .sale-label {
  vertical-align: top;
  padding: 0 0 0 8px;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
  text-align: center;
  width: 100%;
  text-decoration: none;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: block; }

#special-header-promo .sale-label.headerPromoBanner__small {
  display: inline-block;
  *display: inline;
  zoom: 1; }

#special-header-promo .sale-label.headerPromoBanner__large {
  display: none; }

#special-header-promo .sale-label.sale-label--medium {
  font-size: 13px;
  font-weight: 500; }

