body {
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #333333;
  line-height: 1.42857143;
  font-size: 15px;
  background:#f4f4f4;
}
h1,
h2,
h3,
h4,
h5,
h6,
span,
ul,
li,
img {
  padding: 0;
  margin: 0;
}
.container {
  margin-right: auto;
  margin-left: auto;
  width: 1000px;
}
@media (min-width: 1280px) {
  .container {
    width: 1200px;
  }
}

// link
.card{
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.16);
  cursor: pointer;
  &:hover{
    box-shadow: 0 4px 8px rgba(0,0,0,.16);
  }
}
.mylink{
  cursor: pointer;
  font-size: 14px;
}
.mylink::first-letter{
  text-transform: uppercase;
}
.mylink:hover{
  text-decoration: underline;
}

// text
.primaryColor{
  color: $primaryColor
}
.font-bold{
  font-weight: 500;
}
.d-flex{
  display: flex;
}
.d-block {
  display: block;
}
.d-inline-block {
  display: inline-block;
}
.text-center {
  text-align: center;
}
@for $i from 1 through 100 {
  .w-#{$i}{
    width: calc(#{$i} * 100%);
  }
}
@for $i from 13 through 30 {
  .fs-#{$i}{
    font-size: #{$i}px;
  }
}
@for $i from 0 through 100 {
  .pt-#{$i} {
    padding-top: #{$i}px;
  }
  .pb-#{$i} {
    padding-bottom: #{$i}px;
  }
  .pl-#{$i} {
    padding-left: #{$i}px;
  }
  .pr-#{$i} {
    padding-right: #{$i}px;
  }
  .p-#{$i} {
    padding: #{$i}px;
  }

  .mt-#{$i} {
    margin-top: #{$i}px;
  }
  .mb-#{$i} {
    margin-bottom: #{$i}px;
  }
  .ml-#{$i} {
    margin-left: #{$i}px;
  }
  .mr-#{$i} {
    margin-right: #{$i}px;
  }
  .m-#{$i} {
    margin: #{$i}px;
  }
}
